dd-price>\r\n              <div class=\"btn-del\" ><span @click.stop=\"del(i, 1)\">{{''}}</span></div>\r\n          </div>\r\n        </transition-group>\r\n      </mt-tab-container-item>\r\n      <mt-tab-container-item id=\"2\" v-if=\"quotaInfo.buyContract\">\r\n        <transition-group name=\"fade\">\r\n          <div v-for=\"(p,i) in price2\" :key=\"p.id\" v-auto-focus>\r\n              <add-price v-model=\"price2[i]\" :options=\"quotaInfo.upManagers\"></add-price>\r\n              <div class=\"btn-del\" ><span @click.stop=\"del(i, 2)\">{{''}}</span></div>\r\n          </div>\r\n        </transition-group>\r\n      </mt-tab-container-item>\r\n    </mt-tab-container>\r\n    <submit-btn okText=\"\" @okHandle=\"ok\" @cancelHandle=\"cancel\" cancelText=\"\"  />\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nimport { Indicator, MessageBox } from 'mint-ui'\r\nimport {mapBase, mapStatePlus} from '@/utils/mapVmodel'\r\nimport SubmitBtn from '@/components/SubmitBtn'\r\nimport addPrice from '@/components/addPrice'\r\nimport {post} from '@/utils/request'\r\n\r\nconst emptyRow = (type) => {\r\n  return {\r\n    // type: type === 2 ? '' : '',\r\n    type: '',\r\n    price: '',\r\n    comment: '',\r\n    id: new Date().getTime(),\r\n    error: ''\r\n  }\r\n}\r\n\r\nexport default {\r\n  components: {\r\n    addPrice,\r\n    SubmitBtn\r\n  },\r\n  data () {\r\n    return {\r\n      popupVisibele: false,\r\n      price1: [],\r\n      price2: [],\r\n      price1Error: -1,\r\n      price2Error: -1,\r\n      active: -1,\r\n      detailId: ''\r\n    }\r\n  },\r\n  beforeRouteEnter (to, from, next) {\r\n    next(async vm => {\r\n      await vm.$store.dispatch(`${vm.moduleName}/getQuotaInfo`, {contractId: to.query.id})\r\n      vm.defaultActive()\r\n      vm.detailId = to.query.detail_id\r\n    })\r\n  },\r\n  computed: {\r\n    ...mapBase(),\r\n    ...mapStatePlus('quotaInfo')\r\n  },\r\n  methods: {\r\n    defaultActive () {\r\n      let active = -1\r\n      let {quotaInfo} = this\r\n      if (quotaInfo) {\r\n        active = quotaInfo.sellContract ? '1' : '2'\r\n      }\r\n      this.$nextTick(() => {\r\n        this.active = active\r\n      })\r\n    },\r\n    add () {\r\n      this['price' + this.active].push(emptyRow(this.active * 1))\r\n    },\r\n    del (index, active) {\r\n      // let price =\r\n      // if (price.length > 1) {\r\n      // this.$delete(price, index)\r\n      this['price' + active].splice(index, 1)\r\n      // }\r\n    },\r\n    async ok () {\r\n      // Indicator.open()\r\n      let failed = false\r\n      let upQuotaItems = []\r\n      let downQuotaItems = []\r\n      let {moduleName} = this\r\n      let tmpcb = (active, item, index) => {\r\n        if (failed) {\r\n          return true\r\n        }\r\n        // let {type, price, comment} = item\r\n        let msgs = {\r\n          type: '',\r\n          price: ''\r\n        }\r\n        let msg = ''\r\n        for (let key in item) {\r\n          if (key !== 'comment' && item[key] === '') {\r\n            msg = msgs[key]\r\n            failed = true\r\n            break\r\n          }\r\n        }\r\n        if (failed) {\r\n          this.$toptips(msg)\r\n          item.error = new Date().getTime()\r\n          this.active = active\r\n          this['price' + active].splice(index, 1, item)\r\n          failed = true\r\n        } else {\r\n          let pItem = {user_id: item.type.user_id, quota: item.price * 10000 * 100, remark: item.comment}\r\n          active === '1' ? downQuotaItems.push(pItem) : upQuotaItems.push(pItem)\r\n        }\r\n      }\r\n      // if(this.price1.length<=0 && this.price2.length<=0){\r\n      //   this.$toptips('')\r\n      // }\r\n      this.price1.find((...args) => tmpcb('1', ...args))\r\n      this.price2.find((...args) => tmpcb('2', ...args))\r\n      if (!failed) {\r\n        let {contract} = this.quotaInfo\r\n        let submitData = {\r\n          project_id: contract.project_id,\r\n          contract_id: contract.contract_id,\r\n          is_main: contract.is_main\r\n        }\r\n        if (downQuotaItems.length) {\r\n          submitData.downQuotaItems = downQuotaItems\r\n        }\r\n        if (upQuotaItems.length) {\r\n          submitData.upQuotaItems = upQuotaItems\r\n        }\r\n        Indicator.open()\r\n        let ret = await post(`/quota/save`, submitData)\r\n        Indicator.close()\r\n        if (!ret.success) {\r\n          this.$toptips(ret.msg)\r\n        } else {\r\n          await MessageBox.alert('!')\r\n          // location.href = `/quota/detail?contract_id=${contract.contract_id}&is_main=${contract.is_main}`\r\n          this.$router.replace({path: `/${moduleName}/detail`, query: {detail_id: this.detailId, __force: true}})\r\n        }\r\n      }\r\n    },\r\n    cancel () {\r\n      // this['price' + this.active] = [emptyRow(this.active * 1)]\r\n      this['price' + this.active] = []\r\n    }\r\n  },\r\n  directives: {\r\n    autoFocus: {\r\n      inserted (el) {\r\n        // console.log(el)\r\n        el.scrollIntoView()\r\n      }\r\n    }\r\n  }\r\n}\r\n</script>\r\n\r\n<!-- Add \"scoped\" attribute to limit CSS to this component only -->\r\n<style scoped>\r\n.fade-enter-active, .fade-leave-active {\r\n  transition: all .3s;\r\n}\r\n.fade-enter-active {\r\n  transform: translate3d(0,0,0);\r\n}\r\n.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {\r\n  opacity: 0;\r\n  transform: translate3d(-100%,0,0);\r\n}\r\n.main{\r\n    position: absolute;\r\n    top:0;\r\n    left:0;\r\n    right:0;\r\n    bottom:0;\r\n    width:100%;\r\n    height:100%;\r\n    font-size: 14px;\r\n    display: flex;\r\n    flex-direction: column;\r\n    overflow: hidden;\r\n    .category {\r\n      padding: 0 0 20px 0;\r\n    }\r\n    .tab-title{\r\n      display: flex;\r\n      justify-content: center;\r\n      padding: 10px;\r\n      ul{\r\n        display: flex; \r\n        border-radius: 8px;\r\n        border: 1px solid #19B491;\r\n        overflow: hidden;\r\n        font-size: 14px;\r\n        li{\r\n          padding: 5px 15px;\r\n          border-right: 1px solid  #19B491;\r\n          color: #19B491;\r\n        }\r\n        li:last-child{\r\n          border-right:none; \r\n        }\r\n        li.active{\r\n          background-color: #19B491; \r\n          color: #ffffff;\r\n        }\r\n      } \r\n    }\r\n}\r\n.toolbar{\r\n  padding: 5px 10px;\r\n  font-size: 14px;\r\n  z-index: 100;\r\n  width: 100%;\r\n  background-color: #F5F6F8;\r\n  border-bottom: 1px solid #e2e2e2;\r\n  margin-top:2px;\r\n}\r\n.btn-del{\r\n  padding: 5px 10px;\r\n  text-align:right;\r\n}\r\n.tab-container{\r\n  flex: 1;\r\n  overflow-y: auto;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);

